<template>
  <div id="app">
    <b-card
      class="qq-messages"
      header="群消息"
      >
      <div class="messages-group">
        <b-card 
          class="qq-message"
          v-for="(item,index) in currentData" :key="index" :title="item.qq">
          <b-card-text class="qq-content">{{ item.content }}</b-card-text>
        </b-card>
      </div>
      <br/>
      <div class="message-input">
        <b-textarea class="input-content" v-model="inputContent">

        </b-textarea>
        <br/>
        <b-button-group class="input-btns">
          <b-button variant="info" @click="clearMessage">清除消息</b-button>
          <b-button  variant="success" @click="sendMessage">发送消息</b-button>
        </b-button-group>
      </div>
    </b-card>
  </div>
</template>

<script>
// import QqMessage from '@/components/QqMessage.vue'

export default {
  name: 'app'
  ,data: function(){
    return{
      imgUrl:{
        click: require('@/assets/click20x11.jpg')
      }
      ,inputContent:"内容"
      ,path:"ws://localhost:49632/"
      ,path2: "ws://182.254.133.233:49632"
      ,data: []
      ,clickMsg: []
      ,clickXhu: []
      ,myqq: []
      ,currentData: this.data
    }
  }
  ,methods: {
    init: function () {
        if(typeof(WebSocket) === "undefined"){
            alert("您的浏览器不支持socket")
        }else{
            // 实例化socket
            this.socket = new WebSocket(this.path2)
            // 监听socket连接
            this.socket.onopen = this.open
            // 监听socket错误信息
            this.socket.onerror = this.error
            // 监听socket消息
            this.socket.onmessage = this.onMessage
        }
    }
    ,open: function () {
        console.log("socket连接成功")
    }
    ,error: function () {
        console.log("连接错误")
    }
    ,onMessage: function (msg) {
        console.log(msg.data)
        var data =  JSON.parse(msg.data) //JSON.parse(JSON.stringify(msg.data))
        if(typeof data.id ==="string"){
          //alert("是数字")
          return 
        }
        // console.log("typeof ", typeof data.id)
        if(data.event == "message" && data.params.group == "514343378"){
          this.clickMsg.push(data.params)
        }else if(data.event == "message" && data.params.group == "221941055"){
          this.clickXhu.push(data.params)
        }else if(data.event == "message" && data.params.qq == "1019443290"){
          console.log("myqq",data.params)
          this.myqq.push(data.params)
        }
        this.data.push(data.params)
        this.currentData = this.clickMsg
    }
    ,close: function () {
        console.log("socket已经关闭")
    }
    ,sendGroup: function (content) {
        // this.socket.send(JSON.stringify(this.getMsg))
        // this.socket.send('{"method":"sendMessage",id:"2","params":{"type":1,"msgid":"","group":"","qq":"1019443290","content":"1"}}')
        var sendString = this.sendData(2,'514343378','',content)
        this.socket.send(JSON.stringify(sendString))
    }
    ,sendQQ(content){
      var sendString = this.sendData(1,'','1019443290',content)
      console.log("data:",JSON.stringify(sendString))
      this.socket.send(JSON.stringify(sendString))
    }
    ,sendMessage(){
      alert(this.inputContent)
      // this.sendQQ(this.inputContent)
      this.sendGroup(this.inputContent)
    }
    ,clearMessage(){
      this.currentData = []
    }
    ,sendData(type,group,qq,content){
      return{
        id: Math.floor(Math.random()*100000)+''
        ,method: "sendMessage"
        ,params: {
          type
          ,msgid: ''
          ,group
          ,qq
          ,content
        }
      }
    }
  }
  ,mounted: function(){
    this.init()
  }
  ,destroyed () {
        // 销毁监听
        this.socket.onclose = this.close
  }
  ,components: {
    // QqMessage
  }
  ,watch:{
    currentData: function(val, val2){
      // console.log("了解data变化")
      this.$nextTick(()=>{
        //console.log("了解滚动变化")
        var container = this.$el.querySelector('.messages-group')
        container.scrollTop = container.scrollHeight
      })

    }
  }
}
</script>

